.showfade-enter {
  opacity: 0;
  -webkit-transform: translateY(-200px);
      -ms-transform: translateY(-200px);
          transform: translateY(-200px);
}

.showfade-enter.showfade-enter-active {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
  -webkit-transition: all .3s ease-in;
  -o-transition: all .3s ease-in;
  transition: all .3s ease-in;
}

.showfade-leave {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.showfade-leave.showfade-leave-active {
  opacity: 0;
  -webkit-transform: translateY(-200px);
      -ms-transform: translateY(-200px);
          transform: translateY(-200px);
  -webkit-transition: all .3s ease-out;
  -o-transition: all .3s ease-out;
  transition: all .3s ease-out;
}


.button-show-fade {
  position: absolute;
  bottom: 8.1%;
  left: 20px;
  padding: 0 5px;
}

.button-show-fade .show-fade {
  padding: 10px 0 20px 0;
}

.button-show-fade .show-fade > input {
  display: -ms-flexbox;
  display: flex;
  background: transparent;
  margin: 10px 0;
  padding-left: 10px;
  outline: none;
  color: white;
  border: 1px solid white;
  width: 300px;
  height: 37px;
  border-radius: 5px;
}
.button-show-fade .show-fade > input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.568);
}
.button-show-fade .show-fade > input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.568);
}
.button-show-fade .show-fade > input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.568);
}
.button-show-fade .show-fade > input::placeholder {
  color: rgba(255, 255, 255, 0.568);
}

@media only screen and (min-width: 768px) {
  .button-show-fade {
    left: 50px;
  }
}


